@use "../../assets/sass/_variables.scss" as *;

.nav {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0 50px;
    color: $color-black;

&__item {
    height: 30px;
}
}

@media screen and (min-width: 992px) {
.nav {
    grid-column: 1 / -1;
}
}